[class].btn {
  font-family: var(--font-family-primary);
  border-radius: var(--radius-btn);
  font-weight: var(--fw-400);
  white-space: nowrap;
  text-align: center;
  color: inherit;
  background-color: inherit;
  border: 1px solid #0000;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 18px;
  line-height: 1.2;
  transition-property: background-color, color, border-color;
  transition-duration: .2s;
  display: flex;
  position: relative;
}

[class].btn:hover, [class].btn:focus {
  border-color: var(--color-blue-650);
  color: var(--color-brown-800);
}

[class].btn--reverse {
  border-color: var(--color-blue-400);
  color: var(--color-white);
  background-color: var(--color-blue-400);
}

[class].btn--reverse:hover, [class].btn--reverse:focus {
  border-color: var(--color-blue-400);
  background-color: var(--color-white);
  color: var(--color-brown-800) !important;
}

[class].btn--user {
  position: relative;
}

[class].btn--user:before {
  content: "";
  background-image: var(--user);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
  top: 50%;
  left: 5px;
  translate: 0 -50%;
}

[class].btn--user:hover:before, [class].btn--user:focus:before {
  filter: invert();
}

@media only screen and (width <= 475px) {
  [class].btn {
    min-width: auto;
    margin: 0 auto;
  }
}

[class].request-btn {
  font-weight: var(--fw-400);
  color: var(--color-white);
  justify-content: space-between;
  font-size: 18px;
  display: flex;
  position: relative;
}

[class].request-btn:after {
  content: "";
  background-image: var(--arrow);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  width: 10px;
  height: 18px;
  transition-property: filter;
  transition-duration: .2s;
  position: relative;
  right: 0;
}

[class].request-btn:hover:after, [class].request-btn:focus:after {
  filter: invert(0);
}

.arrow.btn {
  justify-content: space-between;
  padding: 17px 24px;
  display: flex;
  position: relative;
}

.arrow.btn:after {
  content: "";
  background-image: var(--arrow);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  width: 15.58px;
  height: 15px;
  transition-property: filter;
  transition-duration: .2s;
  position: relative;
  right: 0;
}

.burger {
  --line-height: 2px;
  --buger-size: 20px;
  --move: calc(var(--buger-size) / 3);
  --move-decrement: calc(var(--move) * -1);
  --transition-time: .5s;
  --lines-color: var(--color-black);
  width: calc(var(--buger-size)  + 5px);
  height: var(--buger-size);
  transition: opacity var(--transition-time) ease;
  background-color: #0000;
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.burger span {
  z-index: -1;
  height: var(--line-height);
  background-color: var(--lines-color);
  transition: transform var(--transition-time) ease, background-color var(--transition-time) ease .3s;
  width: 100%;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger span:before, .burger span:after {
  content: "";
  height: var(--line-height);
  background-color: var(--lines-color);
  width: 100%;
  animation-duration: .4s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
  position: absolute;
  left: 0;
}

.burger span:before {
  transform: translateY(var(--move-decrement)) rotate(0);
}

.burger span:after {
  transform: translateY(var(--move)) rotate(0);
}

.burger:hover {
  opacity: 1;
}

@media only screen and (width <= 992px) {
  .burger {
    display: block;
  }
}

.closed.opened span {
  background-color: var(--lines-color);
  transition: background-color .2s;
}

.closed.opened span:before {
  animation-name: toplineburger;
}

.closed.opened span:after {
  animation-name: bottomlineburger;
}

.opened .nav__inner {
  gap: 40px;
  transform: translateX(0);
}

.opened span {
  background-color: #0000;
  transition: background-color .2s;
}

.opened span:before, .opened span:after {
  animation-duration: .2s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
}

.opened span:before {
  animation-name: toplinecross;
}

.opened span:after {
  animation-name: bottomlinecross;
}

@keyframes toplinecross {
  0% {
    transform: translateY(var(--move-decrement));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes bottomlinecross {
  0% {
    transform: translateY(var(--move));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0)rotate(-45deg);
  }
}

@keyframes toplineburger {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move-decrement)) rotate(0);
  }
}

@keyframes bottomlineburger {
  0% {
    transform: translateY(0)rotate(-45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move)) rotate(0);
  }
}

